h1 {
  background: #000;
  border-radius: 6px;
  color: #fff;
  margin: 0px 20px 80px 0;
  padding: 10px;
  text-decoration: underline;
  width: fit-content;
}

h1 span{
  color: #00ffff;
}

input {
  color: gray;
  background-color: transparent;
  border-radius: 6px;
  border-bottom: 3px solid #00ffff;
  border-top: none;
  border-left: none;
  border-right: none;
  font-size: 1.2rem;
  margin-bottom: 20px;
  padding: 5px 10px;
  width: 100%;
}

h2 {
  color: #00ffff;
  background: #000;
  border-radius: 6px;
  display: block;
  font-weight: bold;
  margin: 40px;
  padding: 10px;
  text-align: center;
  text-transform: uppercase;
}

.card-grid{
  justify-content: center;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  background: #000;
  border: 3px solid #000;
  border-radius: 6px;
  color: #00ffff;
  margin-bottom: 30px;
  margin-right: 30px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
  max-width: 200px;
}

footer {
  background: #000;
  border-radius: 6px;
  padding: 5px;
}

footer p {
  color: #00ffff;
  display: block;
  text-align: center;
}